export default {
	data: function() {
		return {
			// 记录当前索引
			recordList: [],
			status: false,
			current: 0,
			tablist: ["年", "月", "日", "时", "分"],
			pickList: [{
					doSome: "doSomg1"
				},
				{
					doSome: "doSomg2"
				},
				{
					doSome: "doSomg3"
				},
				{
					doSome: "doSomg4"
				},
				{
					doSome: "doSomg5"
				},
			],
			// 日期数组
			year: '',
			mouth: '',
			day: '',
			hour: '',
			minutes: '',
			timeArr: [],
			value: [0]
		}
	},
	props: ['timeList'],
	methods: {
		isEmpty(obj) {
		    for(var key in obj) {
		       if(obj.hasOwnProperty(key))
		           return false;
		       }
		    return true;
		},
		bindChange(e) {
			const doSomes = this.pickList[this.current].doSome;
			this[doSomes](e);
			console.log(e)
			console.log(this.timeArr)
		},

		doSomg1(e) {
			console.log(e)
			const year = e.detail.value;
			this.timeArr[0] =year
		},
		doSomg2(e) {
			const mouth = e.detail.value;
			this.timeArr[1] =mouth
		},
		doSomg3(e) {
			const day = e.detail.value;
			this.timeArr[2] =day
		},
		doSomg4(e) {
			const hour = e.detail.value;
			this.timeArr[3] =hour
		},
		doSomg5(e) {
			const minutes = e.detail.value;
			this.timeArr[4] =minutes
		},

		justClose() {
			this.status = true;
			this.$emit('showStatus', this.status)
		},

		reSelected() {
			this.status = true
			this.$emit('showStatus', this.status)
			
			let isture = false;
			
			console.log(this.timeArr)
			
			// this.timeArr.map((item,index)=>{
				if (this.timeArr.length !==5) {
					isture = true;
					console.log(isture);
				} 
			// })
			
			if(isture) {
				uni.showToast({
					title: "请重新选择时间",
					icon: "none"
				})
			}else{
				this.$emit("showSeletedTime", this.timeArr)
			}
			
		},

		clickTab(index) {
			this.current = index;
			console.log(this.current)
		},
	}
}
